import React from 'react'
import { ListViewAll, ListViewImportant, ListViewUrgent } from './ListView'
import OptionsView from './OptionsView'

export const DISPLAY = {
    OPTIONS: -1,
    LIST_ALL: 0,
    LIST_IMPORTANT: 1,
    LIST_URGENT: 2
}

export default function MainDisplay({template, data}) {
    console.log('render MainDisplay')
    let content = null
    if (template === DISPLAY.OPTIONS) {
        content = <OptionsView/>
    }
    else if (template === DISPLAY.LIST_ALL) {
        content = <ListViewAll/>
    }
    else if (template === DISPLAY.LIST_IMPORTANT) {
        content = <ListViewImportant list_state={data}/>
    }
    else if (template === DISPLAY.LIST_URGENT) {
        content = <ListViewUrgent list_state={data}/>
    }
    return (
        <main className="overflow-x-hidden flex-grow-1 position-relative">
            {content}
        </main>
    )
}

