import React from 'react'

const Init = ({client, setAppContent, createElement, Dashboard, Task}) => {
  console.log('render init')
  const handleFileInputChange = async (event) => {
    const file = event.target.files[0];
    if (!file) return; // If no file selected, return early

    try {
      const fileReader = new FileReader()
      fileReader.onload = (e) => {// eslint-disable-next-line
        eval(e.target.result)
      }
      fileReader.readAsText(file)
    } catch (error) {
      console.error('Error reading file:', error)
    }
  }
  return (
      <>
          <label htmlFor="Upload">Upload (if not cached):</label>
          <input id="Upload" type="file" accept=".js" onChange={handleFileInputChange} />
      </>
  )
}

export default Init