const SettingsForDate = {
    weekday: 'short',
    day: 'numeric',
    month: 'short',
    year: 'numeric'
}

const date_to_app_date_format = (date) => new Intl.DateTimeFormat('en-US', SettingsForDate).format(date)

export const getSpecialNamedDays = function() {
    const today = new Date()
    const today_in_app_format = date_to_app_date_format(today)
    
    const duration24h = (24 * 60 * 60 * 1000)
    const tomorrow = new Date(today.getTime() + duration24h)
    const tomorrow_in_app_format = date_to_app_date_format(tomorrow)
    
    const yesterday = new Date(today.getTime() - duration24h)
    const yesterday_in_app_format = date_to_app_date_format(yesterday)

    return {today, today_in_app_format, tomorrow, tomorrow_in_app_format, yesterday, yesterday_in_app_format}

}

const special_format = (date) => {
    return new Intl.DateTimeFormat('en-US', {
        weekday: 'short',
        day: 'numeric',
        month: 'short'
    }).format(date)
}

export const shown_dateTime = (date, options=DATETIME_OPTIONS.FULL, special_named_days=null) => {

    // use pre-calculated values for today and tomorrow, or none if not provided.
    const {today_in_app_format, tomorrow_in_app_format, yesterday_in_app_format} = special_named_days? special_named_days
        : {today_in_app_format: '', tomorrow_in_app_format: ''}
    
    if (options === DATETIME_OPTIONS.FULL) {
        return date.toLocaleString();
    }
    else if (options === DATETIME_OPTIONS.DATE_ONLY) {
        let formatted_date = date_to_app_date_format(date)

        if (yesterday_in_app_format===formatted_date) {
            return `Yesterday — ` + special_format(date)
        }
        else if (today_in_app_format===formatted_date) {
            return `Today — ` + special_format(date)
        }
        else if (tomorrow_in_app_format===formatted_date) {
            return `Tomorrow — ` + special_format(date)
        }
        else {
            return formatted_date
        }
    }
    if (options === DATETIME_OPTIONS.TIME_ONLY) {
        let str = date.toLocaleTimeString();

        // Remove penultimate 4th to 6th characters
        let slicedStr = str.slice(0, str.length - 6) + str.slice(str.length - 3);
        return slicedStr;
    }
}
export const DATETIME_OPTIONS = {
    FULL: 0,
    DATE_ONLY: 1,
    TIME_ONLY: 2
}