import React from 'react'
import Icon from './Icon'

export default function OptionsView() {
    console.log('render OptionsView')

    return (
        <article className='vstack px-3 py-3'>
            <section className='alert alert-primary'>
                <h6>Version: 0.4</h6>
            </section>
            <hr/>
            <section>
                <h1>Tests</h1>
                <br/>
                <button className="btn btn-primary icon-link icon-link-hover"
                onClick={()=>navigator.vibrate([20])}>
                    navigator.vibrate 20
                    <Icon i={'bi-box-seam'}/>
                </button>
                <button className="btn btn-primary icon-link icon-link-hover"
                onClick={()=>navigator.vibrate([0, 1000, 20])}>
                    navigator.vibrate 0, 1000, 20
                    <Icon i={'bi-box-seam'}/>
                </button>
                
                <button className="btn btn-primary icon-link icon-link-hover"
                onClick={()=>navigator.vibrate([
                    1, 500,
                    2, 500,
                    4, 500,
                    8, 500,
                    16, 500,
                    32, 500,
                    64, 500,
                    128, 500,
                    256, 500,
                    512, 500,
                    1024
                    ])}>
                    navigator.vibrate pulses
                    <Icon i={'bi-box-seam'}/>
                </button>
                <button className="btn btn-primary icon-link icon-link-hover"
                onClick={()=>navigator.vibrate([8, 500, 16])}>
                    navigator.vibrate 8, 500, 16
                    <Icon i={'bi-box-seam'}/>
                </button>
            </section>
        </article>
    )
}