import React, { createContext, useContext, useState } from 'react'
import { ModalContext, xcContext } from './Contexts'
import Modal from './Modal'
import Icon from './Icon'
// import NotificationMaker from './NotificationMaker'
import MainDisplay, { DISPLAY } from './MainDisplay'

const MainDisplayContext = createContext()

const Dashboard = ({xc, client}) => {
    console.log('render dash')
    const [modalContent, setModalContent] = useState(false)
    const modalContextValue = {content: modalContent, setContent: setModalContent}

    const [mainDisplayState, setMainDisplayState] = useState(DISPLAY.OPTIONS)

    // const dropup = (
    //     <div className='nav-item dropup'>
    //         <NavLink dropdown={true} highlight_after_click={false} i='bi-sliders' t='Options'/>                
    //         <ul className="dropdown-menu">
    //             <li><NavLink clickCallback={xc.signOut} i='bi-box-arrow-left' t='Sign Out'/></li>
    //             <li><NotificationMaker port_to_sw={client.port_to_sw} /></li>
    //         </ul>
    //     </div>
    // )

    return (
        <xcContext.Provider value={xc}>
        <ModalContext.Provider value={modalContextValue}>
            <Modal modalContent={modalContent}/>

            <MainDisplay xc={xc} template={mainDisplayState} />

            <MainDisplayContext.Provider value={[mainDisplayState, setMainDisplayState]}>
            <nav className='nav nav-underline nav-justified bg-dark-subtle'>
                <DashNavLink goTo={(DISPLAY.OPTIONS)} 
                    i='bi-sliders' t='Options'/>
                <DashNavLink goTo={(DISPLAY.LIST_ALL)} 
                    i='bi-list-task' t='All'/>
                <DashNavLink goTo={(DISPLAY.LIST_IMPORTANT)} 
                    i='bi-exclamation-triangle-fill' t='Important'/>
                <DashNavLink goTo={(DISPLAY.LIST_URGENT)} 
                    i='bi-hourglass-bottom' t='Urgent'/>
            </nav>
            </MainDisplayContext.Provider>
        </ModalContext.Provider>
        </xcContext.Provider>
    )
}

export default Dashboard

function DashNavLink({goTo=null, i, t}) {
    const [mainDisplayState, setMainDisplayState] = useContext(MainDisplayContext)
    const addClasses = (mainDisplayState===goTo)? 'text-primary' : 'link-secondary'
    return (
        <a onClick={()=>setMainDisplayState(goTo)} href='# '
        className={`nav-link ${addClasses} text-center text-decoration-none`}
        >
            <div className='fs-3'><Icon i={i}/></div>
            <div style={{fontSize: 'small'}}>{t}</div>
        </a>
    )
}