import React, { useRef, useState, useEffect } from 'react'
import Task from './Task'
import Icon from './Icon'

export default function TaskForm ({oldTask=new Task(), handleCloseForm}) {
    console.log('render TaskForm')
    function createTask(event) {
        event.preventDefault()
        const formData = new FormData(event.target)

        const id = oldTask.id
        const taskName = formData.get('TaskName_Input')
        const startDate = formData.get('StartDate_Input')
        const dueDate = formData.get('DueDate_Input')
        const priority = formData.get('Priority_Input')
        const doneDate = oldTask.doneDate

        const newTask = new Task({id, taskName, startDate, dueDate, doneDate, priority})
        
        // if form's checkbox boolean is different from boolean coercion of old doneDate, update with setDone or setNotDone
        const doneIsChecked = formData.has('TaskDone_Input')
        if (doneIsChecked !== doneDate) {
            if (doneIsChecked) newTask.setDone()
            else newTask.setNotDone()
        }

        newTask.write_to_DB()
        handleCloseForm()
    }
    const TaskName_Input_Ref = useRef(null)
    useEffect(() => {
        // Set focus on the element after it's been rendered
        TaskName_Input_Ref.current.focus()
        //TaskName_Input_Ref.current.scrollIntoView({ block: 'start', behavior: 'smooth' })
    }, [])
    
    const DateToInputFormat = (date) => {
        if (!date) return null
        let year = date.getFullYear();
        let month = ('0' + (date.getMonth() + 1)).slice(-2); // Add leading zero if needed
        let day = ('0' + date.getDate()).slice(-2); // Add leading zero if needed
        let hours = ('0' + date.getHours()).slice(-2); // Add leading zero if needed
        let minutes = ('0' + date.getMinutes()).slice(-2); // Add leading zero if needed

        // Format the date in the required format for datetime-local input
        return year + '-' + month + '-' + day + 'T' + hours + ':' + minutes
    }

    const FormButton = function({handler=()=>{}, icon, bg, type='button', children}) {
        return (
            <button type={type} className={`btn btn-lg ${bg}`} onClick={(event)=>{
                event.preventDefault()
                handler()
                handleCloseForm()
            }}><Icon i={icon}/>{children}</button>
        )
    }

    const FormHeader = function({oldTask}) {
        return (
            <div className="card-header">
                {oldTask.taskName?
                    <>
                        <input className="form-check-input fs-1 my-auto mx-2" type="checkbox" value="" id="TaskDone_Input" name="TaskDone_Input" 
                        defaultChecked={oldTask.doneDate? true : false}/>
                        <h2 className="heading">Edit '{oldTask.taskName}'</h2>
                        <FormButton handler={()=>{oldTask.del()}} icon={'bi-trash-fill'} bg={'btn-danger'}/>
                    </>
                    :
                    <h2 className="heading">New task</h2>
                }
            </div>
        )
    }

    return (
        <div className="card modal-card-for-form" >
            <form onSubmit={createTask} id='taskForm'>
                {/* next line renders conditionally */}
                <FormHeader oldTask={oldTask}/>
                <div className="card-body">

                    <input type="text" id="TaskName_Input" name="TaskName_Input" required={true} 
                    placeholder='New Task Name' defaultValue={oldTask.taskName} 
                    className="form-control" ref={TaskName_Input_Ref}/>

                    <br/>
                    <div className='wrap-range-and-dates'>
                        <PriorityInputRange oldPriority={oldTask.priority}/>
                        <div className='wrap-dates'>
                            <label htmlFor="StartDate_Input" className="form-label">Start Date:</label><br/>

                            {/* next line renders conditionally */}
                            <input type="datetime-local" id="StartDate_Input" name="StartDate_Input"  
                            defaultValue={DateToInputFormat(oldTask.startDate)} 
                            className="form-control"/>

                            <br/>
                            <label htmlFor="DueDate_Input" className="form-label">Due Date:</label><br/>

                            {/* next line renders conditionally */}
                            <input type="datetime-local" id="DueDate_Input" name="DueDate_Input" 
                            defaultValue={DateToInputFormat(oldTask.dueDate)} 
                            className="form-control"/>

                            <br/>
                        </div>
                    </div>
                </div>
                <div className="card-footer">
                    <button type="submit" className="btn btn-lg btn-success">
                        { oldTask.id?
                            <><Icon i={'bi-save'}/> Save</>
                            : <><Icon i={'bi-file-earmark-plus'}/> Create</>
                        }
                    </button>
                    <FormButton icon={'bi-x-lg'} bg={'btn-secondary'}> Cancel</FormButton>
                </div>
            </form>
        </div>
    )
}

function PriorityInputRange({oldPriority}) {
    const [priority, setPriority] = useState(oldPriority)
    const showPriority = function(event) {
        setPriority(event.target.value)
    }
    return (
        <div className="wrap-range-vertical">
            <div>
                <label htmlFor="Priority_Input" className="form-label">Priority: {priority}</label>
            </div>
            <datalist id="markers">
                <option value="0" label="0-"></option>
                <option value="3" label="3-"></option>
                <option value="6" label="6-"></option>
                <option value="9" label="9-"></option>
            </datalist>
            <input
                type="range"
                id="Priority_Input"
                name="Priority_Input"
                min="0"
                max="9"
                onChange={showPriority}
                value={priority}
                list="markers"
            />

        </div>
    )
}