import React, { useState, useContext, useEffect, useRef } from 'react'
import { DateTimeNowContext, ListVPRefContext, ModalContext, TaskContext, xcContext } from './Contexts'
import ItemInList, { ListItemName, ListItemDateTime, ListItemCheckbox } from './ItemInList'
import { DATETIME_OPTIONS, shown_dateTime, getSpecialNamedDays } from './DateTimeUtils'
import TaskForm from './TaskForm'
import LoadingSpinner from './LoadingSpinner'

const ListViewBase = function ({children}) {
    const modal = useContext(ModalContext)
    const show_Modal_with_TaskFormSimple = function() {
        const hide_Modal = () => {
            modal.setContent(null)
        }
        const task_form_simple = <TaskForm handleCloseForm={hide_Modal}/>
        modal.setContent(task_form_simple)
    }
    // need to make ref
    const list_vp_ref = useRef()
    if (children.length) {
        return (
        <DateTimeNowContext.Provider value={getSpecialNamedDays()}>
            <div ref={list_vp_ref} id='list-viewport' className='overflow-x-hidden overflow-y-auto h-100'>
                <ListVPRefContext.Provider value={list_vp_ref}>
                <article className='d-flex py-3 flex-column'>
                    {children}
                </article>
                </ListVPRefContext.Provider>
            </div>
            <NewTaskBtn onClick={show_Modal_with_TaskFormSimple}/>
        </DateTimeNowContext.Provider>
    )}
    else {
        return <LoadingSpinner/>
    }
}

export function NewTaskBtn({onClick}) {
    function click_with_vibe() {
        navigator.vibrate(30)
        onClick()
    }
    // className={`btn rounded-circle bg-dark-subtle border-secondary-subtle border-2`}
    return (
        <div className='btn-bubble position-absolute bottom-0 end-0'>
            <div className='btn-bubble-bg'></div>
            <a onClick={click_with_vibe} href='# ' className='use-color-transitions'>
            <svg xmlns="http://www.w3.org/2000/svg" width="3rem" height="3rem" fill="currentColor" className="bi bi-file-earmark-plus-fill" viewBox="0 0 16 16">
                <path d="M9.293 0H4a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V4.707A1 1 0 0 0 13.707 4L10 .293A1 1 0 0 0 9.293 0M9.5 3.5v-2l3 3h-2a1 1 0 0 1-1-1M8.5 7v1.5H10a.5.5 0 0 1 0 1H8.5V11a.5.5 0 0 1-1 0V9.5H6a.5.5 0 0 1 0-1h1.5V7a.5.5 0 0 1 1 0"/>
            </svg>
            </a>
        </div>
    )
}
const useDB_task_list = function(filter, sort_fn) {
    
    const [task_list, set_task_list] = useState(null)
    const xc = useContext(xcContext)
    if (!sort_fn) {
        sort_fn = (arg) => arg
    }
    const handleReply = function(results) {
        let new_task_list = list_from_map(results)
        sort_fn(new_task_list)
        set_task_list(new_task_list)
        console.log('%cupdating list', "color: white; background-color: blue; padding: 5px;")
    }
    useEffect(
        () => {
            xc.get_tasks({filter: filter, callback: handleReply})
        }, // eslint-disable-next-line
        []
    )

    return task_list
}

const formatListToJSX = function({task_list, item_format, format_fn=null}) {
    // default format function
    if (!format_fn) {
        format_fn = (list) => {
            let new_list = []
            list.forEach(
                (task) => {
                    new_list.push(
                        <TaskContext.Provider value={task} key={task.id}>
                            <ItemInList>
                                {item_format? item_format : <>
                                    <ListItemName/>
                                    <ListItemDateTime/>
                                </>}
                            </ItemInList>
                        </TaskContext.Provider>
                    )
                }
            )
            return new_list
        }
    }
    // make formatted_list from task_list if it is not null (falsy)
    let formatted_list = []
    if (task_list) {
        let new_list = Array.from(task_list)
        formatted_list = format_fn(new_list)
    }
    return (
        <ListViewBase>
            {formatted_list}
        </ListViewBase>
    )
}

export function ListViewAll() {

    console.log('render ListViewAll')

    const task_list = useDB_task_list(null, SORT_BY.DUE)
    const item_format = (
        <>
            <ListItemCheckbox/>
            <ListItemName/>
            <ListItemDateTime options={DATETIME_OPTIONS.DATE_ONLY}/>
        </> 
    )
    return formatListToJSX({ task_list, item_format })
    
}

export function ListViewImportant() {

    console.log('render ListViewImportant')
    
    const FILTER_BY = useContext(xcContext).FILTER_BY
    const task_list = useDB_task_list(
        FILTER_BY.STARTED_AND_NOT_DONE(),
        SORT_BY.PRIORITY
    )
    const item_format = (
        <>
            <ListItemName/>
            <ListItemDateTime options={DATETIME_OPTIONS.DATE_ONLY}/>
        </>
    )
    return formatListToJSX({ task_list, item_format })
}

export function ListViewUrgent() {

    console.log('render ListViewUrgent')
    
    const FILTER_BY = useContext(xcContext).FILTER_BY
    const task_list = useDB_task_list(
        FILTER_BY.STARTED_AND_NOT_DONE(),
        SORT_BY.DUE
    )
    const special_named_days = getSpecialNamedDays()
    const format_fn = (list) => {
        let new_list = []
        let last_full_day = null
        list.forEach(
            (task) => {
                let task_due_day = shown_dateTime(task.dueDate, DATETIME_OPTIONS.DATE_ONLY, special_named_days)
                if (task_due_day !== last_full_day) {
                    // add decorations for today and tomorrow
                    let days_from_now = null
                    if (task_due_day.slice(0,5) === 'Today') days_from_now = 0
                    else if (task_due_day.slice(0,5) === 'Tomor') days_from_now = 1
                    new_list.push(
                        <HRWithText text={task_due_day} days_from_now={days_from_now} key={crypto.randomUUID()}/>
                    )
                    last_full_day = task_due_day
                }
                new_list.push(
                    <TaskContext.Provider value={task} key={task.id}>
                        <ItemInList>
                            <ListItemName/>
                            <ListItemDateTime options={DATETIME_OPTIONS.TIME_ONLY}/>
                        </ItemInList>
                    </TaskContext.Provider>
                )
            }
        )
        // remove first child
        // new_list.shift()
        return new_list
    }

    return formatListToJSX({task_list, format_fn})
}

export const list_from_map = (task_map) => {
    // Make list of tasks from map of tasks
    let list = []
    task_map.forEach((value, key)=>{
        list.push(value)
    })
    return list
}

export const SORT_BY = {
    DUE(list) {
        list.sort((a,b)=>a.dueDate - b.dueDate)
        return list
    },
    PRIORITY(list) {
        list.sort((a,b)=>-(a.priority - b.priority))
        return list
    }
}

const HRWithText = function({text, days_from_now}) {
    let addClass = 'text-body-secondary'
    if (days_from_now === 0) addClass = 'badge rounded-pill text-black bg-primary fw-medium opacity-75'
    if (days_from_now === 1) addClass = 'badge rounded-pill text-black bg-secondary fw-medium opacity-75'
    
    return (
    <div className='hstack'>
        <hr className='w-100'/>
        <span className={'flex-shrink-0 mx-2 fs-6 '+addClass}>{text}</span>
        <hr className='w-100'/>
    </div>
)}